import React from 'react';
import { useStateValue } from '../../../../../../state';
import { Label, Container, Button } from './styles';

const DimSizer = ({ id }) => {
    const [state, setState] = useStateValue();

    const addDim= () => {
        let newRow = [];
        var i;
        for (i = 0; i < state.mats[id][0].length; i++) {
            newRow.push(0);
        }
        let newMat = state.mats[id].map(inner => inner.slice());
        newMat.push(newRow);
        newMat.forEach(row => row.push(0));
		setState({...state, mats: {...state.mats, [id]: newMat}});
    }
    
    const removeDim = () => {
        if (state.mats[id].length > 1) {
            let newMat = state.mats[id].map(inner => inner.slice());
            newMat.pop();
            newMat.forEach(row => row.pop());
            setState({...state, mats: {...state.mats, [id]: newMat}});
        }
    }
    

	return (
        <Container>
            <Label>Dimension: </Label>
            <Button onClick={() => removeDim()}>-</Button>
            <Label>/</Label>
            <Button onClick={() => addDim()}>+</Button>
        </Container>
	);
}

export default DimSizer